@import '../buttons';
@import '../_inputs';

.landingContainer{
    position: absolute;
    width: 100%;
    overflow-x: hidden;
}

.landingContainer__grid{
    margin: 120px 0;
    position: relative;
    width: 100%;
    padding: 100px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.landingContainer__grid_text{
    width: 40%;
    height: 100%;
    padding: 0 3rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 30px;
}
.landingContainer__image{
    position: relative;
    width: 60%;
    padding: 20px 0;
    img{
        position: absolute;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.row-reverse{
    flex-direction: row-reverse;
}

@media screen and (max-width: 600px) {
    .landingContainer__grid{
        text-align: center;
        margin: 190px 0;
        padding: 30px;
        flex-direction: column;
        gap: 100px;

        &:nth-child(2){
            flex-direction: column-reverse;
        }
    }
    .landingContainer__grid_text{
        width: 90%;
        padding: 0 2rem;
        gap: 20px;
        justify-content: center;
        align-items: center;
    }
    .landingContainer__image{
        position: relative;
        width: 90%;
        padding: 50px 0;
    }
        
}

