@import '../vars';
@import '../buttons';

.header{
    color: $colorWhite;
    position: relative;
    width: 100%;
    min-height: 100vh;
    .header__bg{
        z-index: -10;
        position: absolute;
        top: 0;
        left: 50%;
        width: 1440px;
        transform: translate(-50%, 0);
    }
}
.header__container{
    width: 100%;
    height: 100%;
    z-index: 100;
}
.header__text{
    margin-top: 100px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    h1, p{
        padding: 0 1rem;
    }
}


@media screen and (max-width: 600px) {
    .header{
        font-size: .8rem;
    }
}