.features{
    padding-top: 300px;
    position: relative;
    width: 100%;
    min-height: 100vh;
}
.features__container{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 90px;
}
.features__block-1{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h2, p{
        padding: 0 1rem;
        text-align: center;
    }
    h2{
        margin-bottom: 20px;
        font-size: 2.2rem;
    }    
    p{
        font-size: 1.6rem;
    }
}

.features__block-2{
    display: flex;
    justify-content: center;
    align-items: center;
}

.features__block-item{
    text-align: center;
    width: 25%;
    padding: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
}
.features__block-item_image{
    position: relative;
    width: 100%;
    padding: 20px 0;
    img{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}


@media screen and (max-width: 600px) {
    .features{
        // font-size: .8rem;
        h2{
            font-size: 1.8rem;
        }    
        p{
            font-size: 1.2rem;
        }
    }

    .features__block-2{
        flex-direction: column;
    }

    .features__block-item{
        width: 80%;
    }
}



