.partners{
    padding-top: 100px;
    position: relative;
    width: 100%;
    min-height: 100vh;
}
.partners__container{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 90px;
}
.partners__block-1{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h2, p{
        padding: 0 1rem;
        text-align: center;
    }
    h2{
        margin-bottom: 20px;
        font-size: 2.2rem;
    }    
    p{
        font-size: 1.6rem;
    }
}

.partners__block-2{
    padding: 50px 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.partners__logo{
    position: relative;

    img{
        width: 100%;
    }
}


@media screen and (max-width: '600px') {
    .partners{
        padding-top: 50px;
    }    
    .partners__block-1{
        h2{
            font-size: 1.6rem;
        }    
        p{
            font-size: 1.2rem;
        }
    }
 
    .partners__block-2{
        padding: 0px 50px;
    }        
}

