@import '../vars';
@import '../buttons';

.pricing{
    background-color: $colorSecondary;
    color: $colorWhite;
    position: relative;
    width: 100%;
    padding-top: 100px;
    padding-bottom: 100px;
    padding-left: 0 !important;
}

.pricing__container{
    h2, h4{
        text-align: center;
        padding: 0 1rem;
    }
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 40px;
}

.pricing__card_container{
    gap: 3%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pricing__card_item{
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    background-color: $colorWhite;
    color: $colorBlack;
    width: 20%;
    padding: 40px 20px;
}
.pricing__card_item-price{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

@media screen and (max-width: 600px) {
    .pricing{
        padding: 10px;
    }    
    .pricing__container{
        gap: 20px;
    }
    .pricing__card_container{
        gap: 20px;
        flex-direction: column;
    }
    .pricing__card_item{
        border-radius: 10px;
        width: 80%;
        padding: 20px 10px;
    }
       
}