@import '../vars';
@import '../buttons';
@import '../inputs';

.contact{
    position: relative;
    width: 100%;
    padding-top: 100px;
}
.contact__container{
    text-align: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 40px;
    h2, h4{
        text-align: center;
        padding: 0 1rem;
    }
}

.contact__block{
    gap: 10px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.contact__block-form{
    width: 100%;
    margin-bottom: 2rem;

    form{
        input, textarea{
            width: 100%;
        }
        padding: 20px;
        border-radius: 10px;
        border: 1px solid $colorPrimary ;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 15px;
    }
}

.contact__block-map{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    overflow: hidden;
    gap: 1rem;

    img{
        width: 400px;
    }
}
.contact__block-map_items{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    div{
        padding: 3px;
        flex: 1;
    }
    div img{
        width:15px;
    }
    div p{
        font-size: .7rem;
    }
}

@media screen and (max-width: 600px) {
    .contact__block{
        flex-direction: column;
    }
}
@media screen and (max-width: 450px) {
    .contact__block-map_items{
        flex-direction: column;
        gap: 10px;
        div{
            
        }
        div img{
            width:15px;
        }
        div p{
            font-size: .7rem;
        }
    }
}