.testimonials{
    width: 100%;
    padding: 100px 0;
    position: relative;
}
.testimonials__container{
    text-align: center;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 40px;
    h2, p{
        padding: 0 1rem;
        justify-content: center;
    }
}
.testimonials__image{
    width: 100px;
}

.testimonial__avatar{
    text-align: left;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.testimonial__avatar-image{
    position: relative;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    background: #aaa;

    img{
        position: absolute;
        width: 100%;
    }
}