.navbar{
    position: relative;
    width: 100%;
}
a{
    color: #fff;
    text-decoration: none;
}

.navbar__container{
    padding: 30px 0 30px 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
}

.navbar__toolbar{
    margin-left: 2rem;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
}
.navbar__toolbar-mobile{
    z-index: 1000;
    height: 100vh;
    width: 100%;
    font-size: 1rem;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    padding: 2rem;
    background-color: rgba(#000, .9);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: .7rem;
    div{
        margin-bottom: 2rem;
    }
}
.navbar__toolbar-close{
    position: absolute;
    font-size: .8rem;
    top: 30px;
    right: 50px;
}
.navbar__toolbar-icon{
    margin-left: 2rem;
    width: 100%;
}

.navbar__logo{
    height: 100%;
    position: relative;
    min-width: 150px;
    img{
        width: 100%;
    }
}
.navbar__socialmedia{
    margin-right: 2rem;
    position: relative;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 10px;
}
.navbar__socialmedia_item{
    height: 100%;
    width: 20px;
    position: relative;
    img{
        width: 100%;
    }
}

.navbar__item_text:hover, .navbar__socialmedia_item:hover{
    border-bottom: 1px solid #fff;
}

