.video{
    position: relative;
    width: 100%;
    padding: 100px 0 0 0;
}
.video__container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.false__video{
    position: relative;
    width: 80%;
    height: 400px;
    overflow: hidden;

    img{
        height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}